@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Roboto+Slab:wght@800&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Spartan', sans-serif;
}

:root{
    --PrimaryColor: hsl(225, 50%, 48%);
    --SecondaryColor: hsl(180, 17%, 95%);
    --colorOne: hsl(15, 74%, 70%);
    --colorTwo: hsl(28, 100%, 82%);
    --HoverColor: hsl(225, 56%, 59%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 12%);
    --textColor: hsl(240, 1%, 48%);
    --borderColor: hsl(0, 0%, 83%);
    --whiteColorDeam: hsl(0, 0%, 93%);
    --greyBg: hsl(0, 0%, 96%);
    --greyText: rgb(145, 145, 145);
    --inputColor: hsl(330, 12%, 97%);
    --boxShadow: rgba(0,0,0, 0.1) 0px 10px 15px -3px, rgba(0, 0 ,0 ,0.05) 0px 4px 6px -2px;
    --transition: 0.4s ease-in-out;
}

body{
    background: var(--whiteColor);
}

a{
    text-decoration: none;
}

li{
    list-style: none;
    cursor: pointer;
}

.section{
    padding: 4rem 0 2rem;
}

.container{
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}

.icon{
    font-size: 1.3rem;
    cursor: pointer;
}

.flex{
    display: flex;
    align-items: center;
}

.grid{
    display: grid;
    align-items: center;
}

.none{
    display: none;
}

.btn{
    padding: 0.7rem 1.5rem;
    background-color: var(--PrimaryColor);
    border: none;
    outline: none;
    border-radius: 3rem;
    cursor: pointer;
    color: var(--whiteColor);
    &:hover{
        background: var(--HoverColor);
    }
    a{
        color: var(--whiteColor);
        font-weight: 500;
    }
}

img, video{
    width: 100%;
    height: auto;
}

input{
    font-size: 100%;
    color: var(--greyText);
}

p{
    line-height: 22px;
    font-size: 13px;
    color: var(--greyText);
}

h4{
    padding: 1rem 0;
    font-weight: 700;
    color: var(--blackColor);
}

h2{
    font-size: 25px;
    padding: 1rem 0;
    color: var(--blackColor);
}

//NavBar styles
.navBar{
    position: fixed;
    flex-direction: column;
    width: 100%;
    z-index: 1000;

    &__One, &__Two{
        justify-content: space-between;
        padding: 1rem;
        width: 100%;
    }
    
    &__One{
        background: var(--SecondaryColor);

        .icon:hover{
            color: var(--PrimaryColor);
        }

        .atb{
            gap: 1rem;

            span{
                color: var(--blackColor);
                font-size: 13px;
                cursor: pointer;

                &:hover{
                    color: var(--PrimaryColor);
                    font-weight: 700;
                }
            }
        }

        .utility{
            gap: 2rem;
            li{
                gap: .5rem;
                color: var(--greyText);
            }
            @media screen and (max-width: 440px){
                display: none;
            }
        }
    }

    &__Two{
        display: flex;
        align-items: center;
        justify-content: space-between;
        top: 3rem;
        padding: 1.5rem 1rem;
        position: fixed;
        transition: 0.3s ease-in-out;
        
        .logoDiv{
            .logo{
                width: 20%;
            }
        }
    }

    &__Menu{
        @media screen and (min-width: 768px){
            display: flex;
            gap: 1rem;
        }
        .menu{
            gap: 2rem;
            li{
                color: var(--greyText);
            }
        }
    }

    @media screen and (max-width: 768px){
        &__Menu{
            position: absolute;
            background: var(--SecondaryColor);
            padding: 1.5rem;
            border-radius: 10px;
            top: 5rem;
            right: -50%;
            width: 80%;
            transform: translateX(50%);
            z-index: 2000;
            box-shadow: var(--boxShadow);
            border: 3px solid var(--whiteColor);
            transition: var(--transition);

            .menu{
                flex-direction: column;
                gap: .5rem;
                text-align: center;

                .listItem{
                    padding: .5rem 0;
                    cursor: pointer;
                    color: var(--blackColor);

                    &:hover{
                        color: var(--PrimaryColor);
                        font-weight: 700;
                    }
                }
            }

            .btnOne{
                display: block;
                justify-content: center;
                margin: auto;
                margin-top: 1rem;
            }
        }
    }

    .toggleIcon{
        .icon{
            font-size: 30px;
            color: var(--blackColor);
        }
        @media screen and (min-width: 768px){
            display: none;
        }
    }

    .showNavBar{
        right: 50%;
        transition: .5s ease-in-out;
    }

    .navbarWithBg{
        background: var(--SecondaryColor);
        padding: .5rem 1rem;
        box-shadow: var(--boxShadow);
    }
}

//Home styling
.home{
    padding-top: 10rem;
    text-align: center;
    gap: 2rem;
    flex-direction: column;

    .mainText{
        color: var(--blackColor);
        font-weight: 700;
        font-size: 15px;
        line-height: 2.5rem;
    }

    .homeImages{
        width: 100%;
        margin: 1rem auto 0;
        justify-content: center;
        align-items: center;
        position: relative;
        border-radius: 5rem;

        .videoDiv{
            width: 100%;
            .video{
                border-radius: 10rem;
            }
        }
    }
}

//Search styling
.search{
    &__Container{
        width: 100%;
        padding: 1.5rem;
        border: 2px solid var(--greyBg);
        border-radius: 1rem;
        row-gap: 2rem;
        
        .btns{
            background: var(--SecondaryColor);
            padding: .5rem;
            border-radius: 1rem;
            flex-direction: column;
            width: 100%;
            margin: auto;

            .singleBtn{
                padding: .8rem 1.5rem;
                font-size: 13px;
                border-radius: 3rem;
                width: 80%;
                text-align: center;
                cursor: pointer;

                &:hover{
                    background: var(--PrimaryColor);
                    color: var(--whiteColor);
                    font-weight: 500;
                    transition: .4s ease;
                }
            }
        }
    }
    &__Inputs{
        width: 100%;
        justify-content: center;
        flex-direction: column;
        gap: 1.5rem;

        .singleInput{
            .iconDiv{
                padding: 16px 10px;
                border-radius: 3rem;
                background: var(--SecondaryColor);
                margin-right: 1rem;

                .icon{
                    color: var(--blackColor);
                    opacity: 0.7;
                }
            }

            .texts{
                h4{
                    padding-top: 0;
                    padding-bottom: .7rem;
                    color: var(--blackColor);
                    font-weight: 700;
                }

                input{
                    width: 95%;
                    border: none;
                    outline: none;
                    background: transparent;

                    &::placeholder{
                        font-size: 12px;
                        opacity: 0.5;
                    }
                }
            }
        }
    }
}

//support styling
.support{
    .sectionContainer{
        .tittlesDiv{
            text-align: center;
            small{
                letter-spacing: .8rem;
                text-transform: uppercase;
                font-size: 13px;
                color: var(--blackColor);
                line-height: 1.5rem;
            }
        }

        .infoDiv{
            margin-top: 2rem;
            gap: 1.5rem;
            
            .textDiv{
                gap: 1.5rem;

                .singleInfo{
                    .number{
                        background: var(--PrimaryColor);
                        padding: 5px 12px;
                        width: 40px;
                        border-radius: 3rem;
                        text-align: center;
                        font-size: 13px;
                        font-weight: 700;
                        color: var(--whiteColor);
                    }

                    .colorOne{
                        background: var(--colorOne);
                    }
                    .colorTwo{
                        background: var(--colorTwo);
                    }
                }
            }

            .imgDiv{
                margin: auto;
                display: flex;
                gap: 1rem;
                img{
                    width: 100%;
                    max-width: 420px;
                    height: 636px;
                    border-radius: 2rem;
                    @media screen and (max-width: 768px){
                        height: 200px;
                        max-width: 200px;
                    }
                    @media screen and (max-width: 480px){
                        height: 150px;
                        max-width: 150px;
                    }
                }
            }
        }
    }
}

//info styling
.info{
    background: var(--SecondaryColor);
    &__container{
        .titleDiv{
            flex-direction: column;
            text-align: center;
            gap: 1.5rem;
            justify-content: space-between;
        }

        .cardsDiv{
            gap: 1.5rem;
            margin-top: 3rem;

            .singleCard{
                background: var(--whiteColor);
                padding: 3rem 2rem;
                border-radius: 10rem;
                gap: 1rem;
                text-align: center;
                margin: auto;
                height: 320px;
                width: 90%;
                max-width: 250px;

                .iconDiv{
                    justify-content: center;
                    margin: auto;
                    background: var(--PrimaryColor);
                    width: max-content;
                    height: 55px;
                    padding: 1rem;
                    border-radius: 3rem;

                    .icon{
                        color: var(--whiteColor);

                    }
                }

                .cardTitle{
                    color: var(--blackColor);
                    font-weight: 700;
                }

                .colorOne{
                    background: var(--colorOne);
                }

                .colorTwo{
                    background: var(--colorTwo);
                }
            }
        }
    }
}

//lounge styling
.lounge{
    .sectionContainer{
        gap: 1.5rem;
        
        .imgDiv{
            margin: auto;
            display: flex;
            gap: 2.5rem;
            @media screen and (max-width: 768px){
                gap: 1.5rem;
            }
            
            img{
                width: 100%;
                max-width: 430px;
                height: 650px;
                border-radius: 5rem;
                @media screen and (max-width: 768px){
                    height: 150px;
                    width: 100px;
                }
            }
        }

        .textDiv{
            .grids{
                margin-top: 1rem;
                gap: 1.5rem;

                .singleGrid{
                    .gridTitle{
                        display: block;
                        padding-bottom: .5rem;
                        font-weight: 500;
                        color: var(--blackColor);
                    }
                }
            }
        }
    }
}

//travelers styling
.travelers{
    .sectionContainer{
        text-align: center;

        .travelersContainer{
            margin-top: 1rem;
            gap: 1.5rem;

            .singleTraveler{
                position: relative;
                height: 320px;
                width: 85%;
                max-width: 250px;
                overflow: hidden;
                border-radius: 10rem;
                gap: 1rem;
                margin: auto;

                .destinationImage, .travelerImage, .travelerName{
                    position: absolute;
                }

                .destinationImage{
                    height: 100%;
                    width: 100%;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    object-fit: cover;
                }

                .travelerImage{
                    height: 50px;
                    width: 50px;
                    object-fit: cover;
                    border-radius: 50%;
                    bottom: 30px;
                    left: 40%;
                    transform: translateX(-50%);
                    border: 5px solid rgba(225, 225, 225,0.637);
                    transition: var(--transition);
                    animation: animated 2.3s infinite;
                }

                @keyframes animated {
                    0%{
                        transform: translateY(0px);
                    }
                    50%{
                        transform: translateY(8px);
                    }
                    100%{
                        transform: translateY(0px);
                    }
                }

                .travelerName{
                    bottom: -100%;
                    width: 100%;
                    height: max-content;
                    background: var(--whiteColor);
                    padding: 1.5rem 1rem;
                    transition: var(--transition);
                    animation: animatedBorders 2.3s infinite;

                    span{
                        display: block;
                        color: var(--blackColor);
                        font-weight: 700;
                    }

                    p{
                        color: var(--PrimaryColor);
                        font-style: italic;
                    }
                }

                @keyframes animatedBorders {
                    0%{
                        border-radius: 0rem;
                    }
                    50%{
                        border-radius: 2rem;
                    }
                    100%{
                        border-radius: 0rem;
                    }
                }

                &:hover{
                    .travelerName{
                        bottom: 0;
                        transition: 1s ease-in-out;
                    }
                    .travelerImage{
                        bottom: 110px;
                        transition: 1s ease-in-out;
                    }
                }
            }
        }
    }
}

//subscribers styling
.subscribers{
    background: var(--SecondaryColor);

    .sectionContainer{
        text-align: center;

        .inputDiv{
            margin-top: 1rem;
            flex-direction: column;
            background: var(--whiteColor);
            border-radius: 1rem;

            input{
                border: none;
                outline: none;
                background: transparent;
                padding: 1rem 1.5rem;

                &::placeholder{
                    font-size: 13px;
                    opacity: .5;
                }
            }

            .btn{
                width: 85%;
                @media screen and (min-width: 480px){
                    margin-bottom: 0;
                }
                margin-bottom: 1rem;
            }
        }
    }
}

//footer styling
.footer{
    background: var(--SecondaryColor);

    .sectionContainer{
        gap: 2.5rem;

        .gridOne{
            gap: 1rem;
            justify-content: center;
            margin: auto;

            .logoDiv{
                .logo{
                    width: 20%;
                }
            }

            .socialIcon{
                margin-top: 1rem;
                gap: 1rem;

                .icon{
                    height: 30px;
                    width: 30px;
                    border-radius: 10rem;
                    border: 1px solid var(--borderColor);
                    box-shadow: var(--boxShadow);
                    padding: .4rem;
                    color: var(--greyText);

                    &:hover{
                        background: var(--PrimaryColor);
                        transform: translateY(-7px);
                        transition: var(--transition);
                        border: 1px solid var(--PrimaryColor);
                        color: var(--whiteColor);
                    }
                }
            }
        }

        .footerLinks{
            display: grid;

            .linkTitle{
                color: var(--blackColor);
                font-weight: 500;
                display: block;
                padding-bottom: 1rem;
            }

            li a{
                display: block;
                color: var(--greyText);
                transition: var(--transition);
                padding: .3rem 0;

                &:hover{
                    transform: translateX(7px);
                    color: var(--PrimaryColor);

                }
            }
        }
    }

    .copyRightDiv{
        justify-content: center;
        margin-top: 1.5rem;
        padding: 1rem;
        gap: 1rem;
        border-top: 1px solid var(--greyText);
        text-align: center;
    }
}

//media screen styling
@media screen and (min-width: 480px){
    .home{
        padding-top: 12rem;
    }

    .search{
        &__Inputs{
            display: grid;
            grid-template-columns: repeat(2,1fr);
        }
    }

    .travelers{
        .sectionContainer{
            .travelersContainer{
                grid-template-columns: repeat(2,1fr);
            }
        }
    }

    .subscribers{
        .sectionContainer{
            max-width: 80%;
            margin: auto;

            .inputDiv{
                flex-direction: row;
                padding: 0 .5rem;
                border-radius: 3rem;
            }
        }
    }

    .footer{
        .sectionContainer{
            align-items: flex-start;
            grid-template-columns: repeat(2, 1fr);

            .footerLinks{
                justify-content: center;
            }
        }
    }
}

@media screen and (min-width: 570px){
    .home{
        .mainText{
            width: 80%;
        }

        .homeImages{
            .videoDiv{
                .video{
                    border-radius: 15rem;
                }
            }
        }
    }

    .search{
        &__Container{
            .btns{
                border-radius: 3rem;
                flex-direction: row;
            }
        }
        &__Inputs{
            .btnBlock{
                width: 30%;
            }
        }
    }

    .support{
        .sectionContainer{
            .infoDiv{
                .textDiv{
                    margin-top: 2rem;
                    grid-template-columns: repeat(2, 1fr);
                }
            }
        }
    }

    .info{
        &__container{
            .titleDiv{
                flex-direction: row;
                h2{
                    text-align: left;
                    max-width: 500px;
                }
            }

            .cardsDiv{
                grid-template-columns: repeat(2, 1fr);
            }
        }
    }

    .lounge{
        .sectionContainer{
            .textDiv{
                .grids{
                    grid-template-columns: repeat(2, 1fr);
                }
            }
        }
    }

    .subscribers{
        .sectionContainer{
            .inputDiv{
                justify-content: space-between;

                .btn{
                    max-width: max-content;
                }
            }
        }
    }

    .footer{
        .sectionContainer{
            padding-top: 2rem;
            grid-template-columns: repeat(3, 1fr);
        }
    }
}

@media screen and (min-width:1024px){
    p{
        font-size: 15px;
        line-height: 22px;
    }

    h4{
        font-size: 20px;
    }
    h2{
        font-size: 27px;
    }
    .section{
        padding: 6rem 0 4rem;
    }
    .container{
        width: 75%;
        margin: auto;
    }
    .navBar{
        &__One, &__Two{
            padding-bottom: 1.5rem;
        }

        .navbarWithBg{
            padding: 0 5rem;
        }
    }
    .support{
        .sectionContainer{
            .infoDiv{
                .imgDiv{
                    margin-left: -10%;
                }
                .textDiv{
                    margin-top: 2rem;
                    grid-template-columns: repeat(3, 1fr);
                }
            }
        }
    }
    .info{
        &__container{
            .cardsDiv{
                grid-template-columns: repeat(3, 1fr);
            }
        }
    }
    .footer{
        .sectionContainer{
            padding-top: 1rem;
            width: 75%;
            grid-template-columns: repeat(4, 1fr);
        }
    }
    .travelers{
        .sectionContainer{
            .travelersContainer{
                display: flex;
            }
        }
    }
}